.gridContainer {
  max-width: 1280px;
  margin: 0 auto;
  padding: 32px 128px 64px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.title {
  font-family: 'Inter', 'Noto Sans JP', 'Noto Sans SC', sans-serif;
  font-size: 24px;
  font-weight: 600;
  line-height: 32px;
  color: #101828;
  letter-spacing: 0.0703px;
  position: relative;
  height: 33px;
}

.count {
  font-family: 'Inter', sans-serif;
  font-size: 18px;
  font-weight: 600;
  line-height: 28px;
  color: #6a7282;
  letter-spacing: -0.4395px;
  position: absolute;
  left: 96px;
  top: 8px;
}

.sortSelect {
  width: 150px;
  height: 38px;
  border: 1px solid #d1d5dc;
  border-radius: 10px;
  padding: 0 12px;
  font-family: 'Inter', 'Noto Sans JP', sans-serif;
  font-size: 16px;
  color: #364153;
  background-color: #ffffff;
  cursor: pointer;
  outline: none;
}

.sortSelect:hover {
  border-color: #155dfc;
}

.sortSelect:focus {
  border-color: #155dfc;
  box-shadow: 0 0 0 2px rgba(21, 93, 252, 0.1);
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px;
}

@media (max-width: 1200px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .gridContainer {
    padding: 24px;
  }

  .grid {
    grid-template-columns: 1fr;
  }

  .header {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
  }

  .sortSelect {
    width: 100%;
  }
}
